* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --primary-color: #1f4e79;
  --secondary-color: #f1f1f1;
  --tertiary-color: #c9a46a;
  --primary-black: #333;
  --secondary-black: #7d7d7d;
  --white: #fff;
}

body {
  background-color: var(--secondary-color);
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  display: flex;
}

header {
  color: var(--white);
  background-image: linear-gradient(to left, #fff0 0%, #000 50%);
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  padding: 16px;
  position: relative;
}

header > nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.hamburger-menu {
  background-color: var(--secondary-color);
  width: 100%;
  color: var(--primary-color);
  display: none;
  position: absolute;
  top: 60px;
  right: 0;
}

.hamburger-menu ul {
  list-style: none;
}

.logo {
  flex-direction: row;
  justify-content: start;
  align-items: center;
  display: flex;
}

#logo-img {
  height: 30px;
}

.logo div {
  margin-left: 8px;
  font-size: 16px;
  font-weight: 400;
}

header > nav > ul {
  flex-direction: row;
  justify-content: end;
  align-items: center;
  display: flex;
}

#btn-menu {
  width: 30px;
  display: none;
}

.active {
  display: block;
}

header a {
  color: inherit;
  text-decoration: none;
}

li {
  cursor: pointer;
  border-bottom: 1px solid #0000;
  padding: 8px 16px;
  font-weight: 600;
  list-style: none;
}

header > nav li:hover {
  color: var(--tertiary-color);
  border-bottom: solid 1px var(--tertiary-color);
}

.hero-section {
  flex-direction: column;
  justify-content: center;
  align-items: start;
  height: 60%;
  padding: 16px;
  display: flex;
  position: relative;
}

.hero-title {
  color: var(--secondary-color);
  margin-bottom: 16px;
  font-size: 40px;
  font-weight: 500;
  line-height: 100%;
}

.hero-subtitle {
  color: var(--secondary-color);
  font-size: 24px;
  font-weight: 400;
  line-height: 100%;
}

.cta {
  background-color: var(--tertiary-color);
  color: var(--secondary-color);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin-top: 32px;
  padding: 8px 16px;
  font-size: 18px;
  font-weight: 700;
  position: absolute;
  bottom: 0;
}

@media (width >= 992px) {
  header {
    height: 500px;
  }
}

@media (width <= 992px) {
  header {
    height: 450px;
  }
}

@media (width <= 768px) {
  header {
    height: 300px;
  }

  .hero-section {
    justify-content: start;
    height: 70%;
  }

  .hero-title {
    font-size: 35px;
  }

  .hero-subtitle {
    font-size: 20px;
  }
}

@media (width <= 576px) {
  header {
    height: 250px;
  }

  header > nav > ul {
    display: none;
  }

  #btn-menu {
    cursor: pointer;
    display: block;
  }

  .hero-section {
    height: 80%;
  }

  .hero-title {
    font-size: 30px;
  }

  .hero-subtitle {
    font-size: 16px;
  }
}
